import React, { useCallback, useState } from "react";
import { Col, Row } from "react-bootstrap";

import OrderItem from "./OrderItem";
import classes from "./OrderList.module.scss";
import Pagination from "../../components/UI/Pagination";

const OrderList = (props) => {
  const { order } = props;
  const [currentPage] = useState(1);
  const [dataLimit] = useState(4);
  const [ordersFilter, setOrderFilte] = useState(order);

  const getPaginatedData = useCallback((page) => {
    const startIndex = `${page ? page : currentPage}` * dataLimit - dataLimit;
    const endIndex = startIndex + dataLimit;
    setOrderFilte(order.slice(startIndex, endIndex));
  }, [currentPage,dataLimit,order]);

  // const orders = order.map(item => <OrderItem key={item.id} id={item.id} title={item.title} image={item.image} desc ={item.description} price={item.price}/>)
  const orders = ordersFilter.map((item) => (
    <OrderItem
      key={item.id}
      id={item.id}
      title={item.title}
      image={item.image}
      desc={item.description}
      price={item.price}
    />
  ));
  return (
    <Row className={classes["order-content"]}>
      {orders}
      {order && (
        <Col md={12} lg={12}>
          <Pagination
            pageNumber={currentPage}
            data={order}
            renderComponent={getPaginatedData}
            pagetCount={Math.round(order.length / dataLimit)}
            pageLimit={1}
            dataLimit={dataLimit}
          ></Pagination>
        </Col>
      )}
    </Row>
  );
};
export default OrderList;
